import moment from 'moment'

export default {
    name: "AddImages",
    props: {
        info: {},
        index: 0
    },    
    data: () => ({  
        success_message_text: 'Se salvaron los datos correctamente!!!',
        error_message_text: '',
        error_message: false,
        success_message: false,      
        image_loaded: false,  
        load: false,                                  
    }),
    watch: {
        className(item) {
            this.info.class_name = item            
            this.$emit('imgStatus', {index: this.index, img: this.info})
        }
    },    
    created() {
        this.image_loaded = (this.info)
    },  
    methods: {                      
        imageSelected(e) {            
            const file = e.target.files[0];
            this.info.url = URL.createObjectURL(file);
            this.image_loaded = true
            
            this.info.image_keyword = file.name.split('.')[0]
            this.info.ext = file.name.split('.')[1]
            this.info.file = file
            this.$emit('imgStatus', {index: this.index, img: this.info})
        }, 
        clearImage(){
            this.info.url = ''
            this.info.image_keyword = ''
            this.info.ext = ''
            this.info.file = ''
            this.image_loaded = false
            this.$refs.file.value = ''
            this.$emit('imgStatus', {index: this.index, img: this.info})
        },                                
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
    }
}
