import Authorization from "../user/auth.service";

const OffersService = {
    getList() {
        try {
            return Authorization.checkLogin(`/offers`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            title: item.title,
                            keyword: item.keyword,
                            description: item.description,
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    }, 
    async getOffer(id) {
        try {
            const { status, data } = await Authorization.checkLogin(`/offer/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },   
    async addUpdOffer(params, is_new = true, hasImage = false) {
        try {            
            params.auth = 'bearer'                  
            const url = (is_new) ? '/offer/create' : `/offer/upd/${params.id}`

            const {status, data} = (!hasImage) ? 
                await Authorization.checkLogin(url, params, 'post') :
                await Authorization.checkLoginImage(url, params.form, 'post')
                
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin('/offers/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default OffersService