import TextsService from "@/services/common/texts.service";

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudTexts",
    data() {
        return {
            error_message_text: '',
            success_message_text: 'La información ha sido enviada satisfactoriamente.',
            error_message: false,
            success_message: false,
            load: false,
            formData: {
                id: '',
                keyword: '',
                description: '',
            },
        }
    },
    created() {
        this.initForm()
    },
    watch: {
        mode() {
            this.initForm()
        },
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },
    methods:{
        initForm() {
            // For upd action
            console.log(this.info)
            if(this.info)
                TextsService.getText(this.info).then(e => {
                    if(this.mode != 'create') {                
                        this.formData.id = e.id
                        this.formData.keyword = e.keyword  
                        this.formData.description = e.description  
                    }
                })  
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {            
            e.preventDefault();            
            if(this.validate(this.formData.keyword)) {                
                this.load = true
                TextsService.addUpdText(this.formData, (this.mode == 'create')).then(res => {
                    this.load = false
                    // if(res) {
                        this.error_message = false
                        this.success_message = true
                        setTimeout(() => {
                            this.success_message = false                    
                        }, 3000)
                    // }                    
                })
            } else {
                this.error_message = true
                this.success_message = false
                setTimeout(() => {
                    this.error_message = false                    
                }, 3000)
                this.error_message_text = 'Faltan datos por llenar'
            }
        },   
        clear() {
            this.formData.keyword = ''
            this.formData.description = ''
        }     
    }    
}
